@import "../../sass/settings/_mixins"
@import "../../sass/settings/_vars"

.weatherPage
    color: $colorWhite
    padding: 40px 0
    .weatherPage-container
        max-width: 1230px
        margin: 0 auto
        padding: 0 15px
        .weatherPage-link-back
            @include flex-center
            font-size: 14px
            color:$colorWhite
            opacity: 0.6
            transition: all .3s ease
            &::before
                font-size: 12px
                padding-right: 5px
                transition: all .3s ease
            &:hover
                opacity: 1
                &::before
                    transform: translateX(-5px)
        .weather-city-name
            margin-top: 20px
            font-weight: 700
            font-size: 32px
            margin-bottom: 20px
        .weatherPage-links
            @include flex-center
            column-gap: 20px
            margin-bottom: 10px
            .weatherPage-link
                padding: 5px 0
                opacity: 0.8
                font-weight: 500
                color: $colorWhite
                padding-bottom: 3px
                position: relative
                &::before
                    content: ''
                    display: block
                    width: 1px
                    height: 15px
                    position: absolute
                    top: 5px
                    left: -10px
                    background: $colorWhite
                    opacity: 0.6
                &::after
                    content:''
                    position: absolute
                    bottom: -3px
                    left: -10%
                    width: 0%
                    height: 1px
                    background: $colorWhite
                    transition: all .3s ease
                &:first-child
                    &::before
                        display: none
                &:hover
                    opacity: 1
                    &::after
                        width: 120%
            .weatherPage-link.active
                opacity: 1
                &::after
                    width: 120%
        .weather-info
            margin-top: 20px
            .weatherPage-header
                display: flex
                column-gap: 5px
                margin-bottom: 10px
                .weather-status
                    @include flex-center
                    column-gap: 40px
                    font-weight: 500
                    font-size: 20px
                    .wheather-icon
                        padding-top: 10px
                        width: 70px
                        height: 70px
                        padding-bottom: 20px
                        img
                            width: 100%
                            height: 100%
                            object-fit: contain
            .weatherPage-body
                @include flex-column
                row-gap: 15px

.weatherPage-favorite
    padding-top: 30px
    .weatherPage-favorite-container
        @include flex-column
        row-gap: 20px
        .weatherPage-favorite-title
            font-size: 20px
            margin-bottom: 10px


.weather-city
    color: $colorWhite
    box-shadow: 0px 5px 10px  0 #000
    padding: 30px 20px
    display: flex
    align-items: center
    justify-content: space-between
    transition: all .3s ease
    &:hover
        //transform: scale(1.01)
        box-shadow: 0px 5px 20px  0 #000
    .weather-header
        flex: 0 0 33.33%
        display: flex
        align-items: center
        column-gap: 0px
        .weather-main
            display: flex
            column-gap: 30px
            .weather-city-name
                position: relative
                @include flex-center
                flex-direction: row-reverse
                color: $colorWhite
                font-size: 18px
                text-transform: uppercase
                font-weight: 600
                &::before
                    margin-bottom: 3px
                    font-size: 14px
                    transform: rotate(180deg)  translateX(15px)
                    opacity: 0
                    transition: all 0.3s ease 0.2s
                &::after
                    content: ''
                    position: absolute
                    bottom: -4px
                    left: 0
                    width: 0%
                    height: 2px
                    background: $colorWhite
                    transition: all 0.3s ease 
                &:hover
                    &::before
                        opacity: 1
                        transform: rotate(180deg)  translateX(-10px)
                    &::after
                        width: 100%
            .weather-status
                font-size: 14px
        .wheather-icon
            width: 40px
            height: 40px
            img
                width: 100%
                height: 100%
                object-fit: contain
    .wheather-temp
        flex: 0 0 33.33%
        display: flex
        align-items: center
        span
            text-transform: uppercase
            font-size: 24px
            font-weight: 600
            &::after
                content: ""
                display: inline-block
                width: 8px
                height: 8px
                margin: 0 0 15px 10px
                border-radius: 50%
                border: 2px solid #fff
        &::after
            content: 'C'
            text-transform: uppercase
            font-size: 24px
            font-weight: 600
            color:#fff
    .weather-feels-like
        flex: 0 0 25%
        font-size: 16px
        position: relative
        span
            font-weight: 600
            padding-left: 10px
            font-size: 24px
            &::after
                content: ""
                display: inline-block
                width: 10px
                height: 10px
                border-radius: 50%
                border: 3px solid #fff
                margin: 0 0 15px 10px
        &::after
            content: 'C'
            font-weight: 600
            font-size: 2.2rem
            color:#fff
    .weather-loading
        width: 100px
        height: 100px
        position: relative
        img
            width: 100%
            height: 100%
            object-fit: contain
    .weather-favorite
        width: 30px
        height: 30px
        flex: 0 0 30px
        background: transparent
        &::before   
            opacity: 0.4
            color: #000
            font-size: 24px
            transition: all .3s ease
        &:hover
            &::before
                opacity: 0.7
                color: #f30000
        &.favorite
            &::before
                opacity: 1
                color: #f30000






._weather-info
    font-size: 16px
    @include flex-center
    column-gap: 10px
    color: #b5b3b3
    span
        font-size: 18px
        font-weight: 500
        color: $colorWhite


._temp
    display: flex
    column-gap: 10px
    span
        &::after
            content: ""
            display: inline-block
            width: 8px
            height: 8px
            margin: 0 0 7px 10px
            border-radius: 50%
            border: 2px solid #fff
    &::after
        content: 'C'
        margin-left: -7px
        text-transform: uppercase
        font-size: 24px
        font-weight: 600
        color: $colorWhite
            
