@import "../../sass/settings/_mixins"
@import "../../sass/settings/_vars"

.header
    padding: 10px 0
    backdrop-filter: blur(10px)
    .header-container
        max-width: 1230px
        margin: 0 auto 
        padding: 0 15px
        display: flex
        align-items: center
        justify-content: space-between
        .header-logo
            height: 40px
            width: 40px
            flex: 0 0 40px
            border-radius: 50%
            transition: all 0.2s linear
            position: relative
            z-index: 5
            margin-right: 10px
            .header-logo-label
                font-size: 16px
                width: 100px
                position: absolute
                top: 12px
                left: 50px
                color: $colorWhite
            &::before,&::after,span 
                position: absolute
                border-radius: 2px
                pointer-events: none
                background: $mainColor
            span 
                top: 3px
                left: 15px
                height: 30px
                width: 4px
                transform: rotate(35deg)
            &::after 
                content: ""
                height: 4px
                width: 18px
                bottom: 9px
                left: 8px
            &::before 
                content: ''
                width: 8px
                height: 4px
                top: 16px
                left: 16px
        .header-nav-list
            @include flex-center
            column-gap: 20px
            .header-nav-list-item
                a
                    font-size: 12px
                    text-transform: uppercase
                    padding: 5px 0
                    color: $colorWhite
                    opacity: 0.6
                    &.active
                        opacity: 1